import returns from '../../common/returns.vue'
export default {
	components:{
		returns
	},
	data() {
		return {
			title: '确认订单',
			data:'',
			type:'',
			address:'',
			shopp:'',
			payment:'',
			payment_id:'',
			show_add:3,
			options:'',
			payment_name: '',
			teamid:''
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		choice(index){ //选择支付方式
			for (let s of this.payment) {
				s.choice = false
			}
			
			this.payment[index].choice = true
			this.payment_id = this.payment[index].id
			this.payment_name = this.payment[index].payment
		},
		payments(){
			let that = this
			if(!this.address){
				uni.showToast({
					icon:'none',
					title:'请添加收货地址'
				})
				return
			}
			if(!this.payment_id){
				uni.showToast({
					icon:'none',
					title:'请选择支付方式'
				})
				return
			}
			let data = {
				aid:that.address.id,
				id:that.shopp.id,
				payment_id:that.payment_id,
				type:that.type,
				user_id:this.$store.state.user.id,
			}
			if(this.teamid) data.teamid = this.teamid
			
			uni.showModal({
			    title: '提示',
			    content: '是否确定支付？',
			    success: function (res) {
			        if (res.confirm) {
			           that.service.entire(that,'get',that.APIconfig.api_root.subhome.threehome.g_Confirm,data,function(self,res){
			           	
			           	if(res.code == 0){
			           		self.service.entire(self,'get',self.APIconfig.api_root.subhome.threehome.g_pay,{
			           			user_id:self.$store.state.user.id,
			           			orderid:res.data.orderid,
			           			payment_id:self.payment_id,
								
			           		},function(selfs,ref){
			           			self.service.order(ref,self,'../../subuser/assemble_order?status=0','pages/subuser/assemble_order?status=1')
			           		})	
			           	}else{
			           		uni.showToast({
			           			icon:"none",
			           			title:res.msg
			           		})
			           	}
			           })
			        } else if (res.cancel) {
			            return
			        }
			    }
			});
			
			
		}
	},
	onLoad(options) {
		this.options = options
		this.type = options.type
		if(options.teamid) this.teamid = options.teamid
		
	},
	onShow() {
		let options = this.options
		let data = {
			id:options.id,
			type:options.type,
			user_id:this.$store.state.user.id,
		}
		if(this.teamid) data.teamid = this.teamid
		this.service.entire(this,'get',this.APIconfig.api_root.subhome.threehome.g_ConfirmDetail,data,function(self,res){
			if(res.code == -100){
				uni.showModal({
				    title: '提示',
				    content: res.msg,
					showCancel:false,
				    success: function (res) {
				        if (res.confirm) {
				           uni.navigateBack({
				               delta:1
				           });
				        }
				    }
				});
				return
			}
			self.data = res.data
			if(!res.data.address){
				self.show_add = 0
			}else{
				self.show_add = 1
			}
			self.address = res.data.address
			self.shopp = res.data.goods
			let data = res.data.payment_list
			for (let s of data) {
				s.choice = false
			}
			self.payment = data
		})
	}
}
